import { Paper, Typography } from '@material-ui/core'
import Grid from '@material-ui/core/Grid'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import HomeIcon from '@material-ui/icons/Home'
import React from 'react'
import PropertyCreditDialog from '../components/Dialogs/PropertyCreditDialog'
import EnhancedTable from '../components/Enhanced/Table/Table'
import Mutation from '../components/Mutation'
import Query from '../components/Query'
import TableWrapper from '../components/TableWrapper'
import { PROPERTY_TABLE } from '../graphql/mutation/local'
import { DELETE_PROPERTIES } from '../graphql/mutation/remote'
import { PROPERTY_TABLE as PROPERTY_TABLE_QUERY } from '../graphql/query/local'
import {
  PROPERTIES,
  PROPERTY_CREDIT_HISTORY,
  PROPERTY_CREDIT_HISTORY_TOTAL,
} from '../graphql/query/remote'
import { defaultState } from '../graphql/resolvers'
import { Routes } from '../Router'
import commanRender, { RENDER_VALUE } from '../utils/commanRender'
import formatDate from '../utils/date'
import getContactJSX from '../utils/getContactJSX'
import toCurrency from '../utils/toCurrency'
const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    fab: {
      position: 'fixed',
      width: '4rem',
      bottom: theme.spacing.unit * 2,
      right: theme.spacing.unit * 2,
    },
  })

export const propertyColumnData = [
  {
    id: 'createdAt',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Created At',
  },
  {
    id: 'name',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Name',
  },
  {
    id: 'location',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Location',
  },
  {
    id: 'price',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Price (₹)',
  },
  {
    id: 'buyer',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Bought By',
  },
  {
    id: 'buyerNumber',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Buyer\'s Phone Number',
  },
  {
    id: 'totalReceivedAmount',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Total Received Amount (₹)',
  },
  {
    id: 'balance',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Balance Amount (₹)',
  },
  {
    id: 'nextDueDate',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Next Due Date',
  },
  {
    id: 'note',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Note',
  },
]
export const propertyExclude = [
  'id',
  'count',
  'createdAt',
  'updatedAt',
  '__typename',
]

interface IProperty extends WithStyles<typeof styles> {
  history?: any
}
const Property = withStyles(styles)((props: IProperty) => {
  const { classes, history, ...restProps } = props
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <Mutation mutation={DELETE_PROPERTIES}>
            {(
              deleteProperties: Function,
              { loading: dloading }: IGraphqlData,
            ) => {
              return (
                <Query query={PROPERTY_CREDIT_HISTORY_TOTAL}>
                  {({ loading, data: { propertyTotal } }: IGraphqlData) => {
                    return (
                      <React.Fragment>
                        <Paper style={{ padding: '2rem', margin: '1rem 0rem' }}>
                          <Grid container spacing={16} justify='space-between'>
                            <Grid item>
                              <Typography variant='subheading'>
                                Total Price (₹):{' '}
                                {toCurrency(propertyTotal.price)}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography variant='subheading'>
                                Total Received Amount (₹):{' '}
                                {toCurrency(propertyTotal.totalReceivedAmount)}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography variant='subheading'>
                                Total Balance (₹):{' '}
                                {toCurrency(propertyTotal.balance)}
                              </Typography>
                            </Grid>
                          </Grid>
                        </Paper>
                        <TableWrapper
                          tableMutation={PROPERTY_TABLE}
                          tableQuery={PROPERTY_TABLE_QUERY}
                          dataQuery={PROPERTIES}
                          defaultState={defaultState.propertyTable}
                          getQueryVariables={e => ({})}
                        >
                          {(
                            tableSettings: typeof defaultState.propertyTable,
                            lmutate: Function,
                            { loading, data, refetch }: IGraphqlData,
                          ) => {
                            return (
                              <EnhancedTable
                                {...restProps}
                                loading={loading || dloading}
                                data={data.properties}
                                title='Properties'
                                settings={tableSettings}
                                count={
                                  (data.properties.length &&
                                    data.properties[0].count) ||
                                  data.properties.length
                                }
                                columnData={propertyColumnData}
                                exclude={propertyExclude}
                                historyable={PROPERTY_CREDIT_HISTORY}
                                credit={
                                  <PropertyCreditDialog
                                    {...restProps}
                                    positiveHandler={(m, e, selected) =>
                                      m({ variables: { ...e, id: selected } })
                                    }
                                  />
                                }
                                primaryButtonIcon={<HomeIcon />}
                                // menu={<EnhancedMenu/>}
                                options={{
                                  onEdit: (e: any) =>
                                    history.push(
                                      Routes.Property.update({
                                        id: e,
                                        edit: true,
                                      }),
                                    ),
                                  onCredit: async (fn: Function, args: any) => {
                                    await fn({ variables: args })
                                    // refetch();
                                  },
                                  onDelete: async (args: any) => {
                                    await deleteProperties({ variables: args })
                                    await lmutate({
                                      variables: defaultState.propertyTable,
                                    })
                                    refetch()
                                  },
                                  onAdd: () =>
                                    history.push(Routes.Property.create()),
                                }}
                                render={commanRender({
                                  match: 'totalReceivedAmount',
                                  renderValue: ({
                                    key,
                                    value,
                                  }: {
                                    key: string
                                    value: any
                                  }) => {
                                    if (key === 'nextDueDate') {
                                      return { value: formatDate(value) }
                                    } else if (key === 'buyerNumber') {
                                      return { value: getContactJSX(value) }
                                    }
                                    const result = RENDER_VALUE({
                                      key,
                                      value,
                                      skip: ['buyerNumber'],
                                    })
                                    return {
                                      ...result,
                                      value: String(value).length
                                        ? result.value
                                        : '-',
                                    }
                                  },
                                })}
                                mutate={async args => {
                                  await lmutate({ variables: args })
                                  // refetch();
                                }}
                              />
                            )
                          }}
                        </TableWrapper>
                      </React.Fragment>
                    )
                  }}
                </Query>
              )
            }}
          </Mutation>
        </Grid>
      </Grid>
    </div>
  )
})

export default Property
